import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'

Vue.use(VueRouter)
const routes = [{
    path: '/index',
    redirect: '/index/home',
    component: require('@/view/index.vue').default,
    children: [{
        path: 'home',
        component: require('@/view/index/home.vue').default
    }, {
        path: 'money',
        component: require('@/view/index/money.vue').default
    }, {
        path: 'friend',
        component: require('@/view/index/friend.vue').default
    }, {
        path: 'my',
        component: require('@/view/index/my.vue').default
    }]
}, {
    path: '/bussiness',
    component: require('@/view/bussiness.vue').default
}, {
    path: '/qrcode',
    component: require('@/view/qrcode.vue').default
}, {
    path: '/zhangben',
    component: require('@/view/zhangben.vue').default
}, {
    path: '/userInfo',
    component: require('@/view/userInfo.vue').default
}, {
    path: '/chat',
    component: require('@/view/chat.vue').default
}, {
    path: '/recharge',
    component: require('@/view/recharge.vue').default
}, {
    path: '/recharge/history',
    component: require('@/view/history.vue').default
}, {
    path: '/game/list',
    component: require('@/view/game.vue').default
}, {
    path: '/cards',
    component: require('@/view/cards.vue').default
}, {
    path: '/order/list',
    component: require('@/view/order/list.vue').default
}, {
    path: '/order/detail',
    component: require('@/view/order/detail.vue').default
}, {
    path: '/state/fail',
    component: require('@/view/state/fail.vue').default
}, {
    path: '/state/success',
    component: require('@/view/state/success.vue').default
}, {
    path: '/message/list',
    component: require('@/view/message/list.vue').default
}, {
    path: '/message/detail',
    component: require('@/view/message/detail.vue').default
}, {
    path: '/zuqiu',
    component: require('@/zuqiu/zuqiu.vue').default
}, {
    path: '/animate',
    component: require('@/view/animate.vue').default
}, {
    path: '/cunkuan',
    component: require('@/view/cunkuan.vue').default
}, {
    path: '*',
    redirect: '/index'
}]
const router = new VueRouter({routes})

// 扩展路由函数保存路由行为状态
let behavior = null
let behaviorGo = null
router.push = function (location) {
    behavior = 'push'
    VueRouter.prototype.push.apply(this, arguments)
}
router.replace = function () {
    behavior = 'replace'
    VueRouter.prototype.replace.apply(this, arguments)
}
router.go = function (n) {
    behavior = 'go'
    behaviorGo = n
    VueRouter.prototype.go.apply(this, arguments)
}
router.beforeEach((to, from, next) => {
    // 页面切换动画逻辑搁置
    let transitionName
    if (behavior === 'push') {
        transitionName = 'forward'
    }
    if (behavior === 'hashchange') {
        transitionName = 'back'
    }
    if (behavior === 'go') {
        if (behaviorGo > 0) {
            transitionName = 'forward'
        } else {
            transitionName = 'back'
        }
    }
    store.commit('updateDirection', transitionName)
    next()
})

// 登录态判断
router.beforeEach((to, from, next) => {
    if (to.meta.login) {
        if (!store.state.user.islogin) {
            return next({
                name: 'login',
                params: {source: to}
            })
        }
    }
    next()
})

export default router



