<script>
import axios from 'axios'
import zuqiuDetail from './detail'
export default {
    data () {
        return {
            current: 0,
            showIndex: null,
            detailIndex: 0,
            showDetail: false,
            list: []
        }
    },
    components: {
        zuqiuDetail
    },
    created () {
        this.getDataList()
    },
    methods: {
        getDataList () {
            axios.get('/api/cur_match.json').then((data) => {
                this.list = data.data
            })
            setInterval(() => {
                axios.get('/api/cur_match.json').then((data) => {
                    this.list = data.data
                })
            }, 15000)
        },
        // 压盘时间轴的进攻数据
        calcZuqiu (item, w) {
            let str = ''
            for (var i = 0; i < item.length; i++) {
                if (item[i][0] === 'home_shotoffgoal') {
                    // '主队射偏'
                    str += `<i class="zs" style="left:${(10 * item[i][1] / 6 / w).toFixed()}%"></i>`
                } else if (item[i][0] === 'home_shotongoal') {
                    // '主队射正'
                    str += `<i class="zs" style="left:${(10 * item[i][1] / 6 / w).toFixed()}%"></i>`
                } else if (item[i][0] === 'away_shotoffgoal') {
                    // '客队射偏'
                    str += `<i class="ks" style="left:${(10 * item[i][1] / 6 / w).toFixed()}%"></i>`
                } else if (item[i][0] === 'away_shotongoal') {
                    // '客队射正'
                    str += `<i class="ks" style="left:${(10 * item[i][1] / 6 / w).toFixed()}%"></i>`
                } else if (item[i][0] === 'home_goal') {
                    // '主队进球'
                    str += `<i class="z" style="left:${(10 * item[i][1] / 6 / w).toFixed()}%"></i>`
                } else if (item[i][0] === 'away_goal') {
                    // '客队进球'
                    str += `<i class="k" style="left:${(10 * item[i][1] / 6 / w).toFixed()}%"></i>`
                }
            }
            return str
        },
        calcFirstHalfZuqiu (item, w) {
            let str = ''
            for (var i = 0; i < item.length; i++) {
                if (item[i][0] === 'home_shotoffgoal' && item[i][2] === 'first') {
                    // '主队射偏'
                    str += `<i class="zs" style="left:${this.bijaio(10 * item[i][1] / 3 / w).toFixed()}%"></i>`
                } else if (item[i][0] === 'home_shotongoal' && item[i][2] === 'first') {
                    // '主队射正'
                    str += `<i class="zs" style="left:${this.bijaio(10 * item[i][1] / 3 / w).toFixed()}%"></i>`
                } else if (item[i][0] === 'away_shotoffgoal' && item[i][2] === 'first') {
                    // '客队射偏'
                    str += `<i class="ks" style="left:${this.bijaio(10 * item[i][1] / 3 / w).toFixed()}%"></i>`
                } else if (item[i][0] === 'away_shotongoal' && item[i][2] === 'first') {
                    // '客队射正'
                    str += `<i class="ks" style="left:${this.bijaio(10 * item[i][1] / 3 / w).toFixed()}%"></i>`
                } else if (item[i][0] === 'home_goal' && item[i][2] === 'first') {
                    // '主队进球'
                    str += `<i class="z" style="left:${this.bijaio(10 * item[i][1] / 3 / w).toFixed()}%"></i>`
                } else if (item[i][0] === 'away_goal' && item[i][2] === 'first') {
                    // '客队进球'
                    str += `<i class="k" style="left:${this.bijaio(10 * item[i][1] / 3 / w).toFixed()}%"></i>`
                }
            }
            return str
        },
        calcSecondHalfZuqiu (item, w) {
            let str = ''
            for (var i = 0; i < item.length; i++) {
                if (item[i][0] === 'home_shotoffgoal' && item[i][2] === 'second') {
                    // '主队射偏'
                    str += `<i class="zs" style="left:${((10 * item[i][1]) / (3 * w) - 100).toFixed()}%"></i>`
                } else if (item[i][0] === 'home_shotongoal' && item[i][2] === 'second') {
                    // '主队射正'
                    str += `<i class="zs" style="left:${((10 * item[i][1]) / (3 * w) - 100).toFixed()}%"></i>`
                } else if (item[i][0] === 'away_shotoffgoal' && item[i][2] === 'second') {
                    // '客队射偏'
                    str += `<i class="ks" style="left:${((10 * item[i][1]) / (3 * w) - 100).toFixed()}%"></i>`
                } else if (item[i][0] === 'away_shotongoal' && item[i][2] === 'second') {
                    // '客队射正'
                    str += `<i class="ks" style="left:${((10 * item[i][1]) / (3 * w) - 100).toFixed()}%"></i>`
                } else if (item[i][0] === 'home_goal' && item[i][2] === 'second') {
                    // '主队进球'
                    str += `<i class="z" style="left:${((10 * item[i][1]) / (3 * w) - 100).toFixed()}%"></i>`
                } else if (item[i][0] === 'away_goal' && item[i][2] === 'second') {
                    // '客队进球'
                    str += `<i class="k" style="left:${((10 * item[i][1]) / (3 * w) - 100).toFixed()}%"></i>`
                }
            }
            return str
        },
        bijaio (a) {
            return a > 100 ? 100 : a
        },
        // 时间轴的时间进度条
        calcTime (s, w) {
            if (s === '半') {
                return {width: '50%'}
            } else if (s === '全') {
                return {width: '100%'}
            } else {
                return {width: (s / w) * 100 + '%'}
            }
        },
        // 上半场时间轴的时间进度条
        calcFirstHalfTime (s, w) {
            if (s === '半') {
                return {width: '100%'}
            } else if (s === '全') {
                return {width: '100%'}
            } else if (s > (w / 2)) {
                return {width: '100%'}
            } else {
                return {width: (s / w * 2) * 100 + '%'}
            }
        },
        calcSecondHalfTime (s, w) {
            if (s === '半') {
                return {width: '100%'}
            } else if (s === '全') {
                return {width: '100%'}
            } else if (s > (w / 2)) {
                return {width: '100%'}
            } else {
                return {width: (s / w * 2) * 100 + '%'}
            }
        },
        // 比赛数据比分
        getRLW (ratea, rateb) {
            return {width: `${((ratea / (ratea + rateb)) * 100).toFixed()}%`}
        },
        // 隐藏相同联赛名
        showTitle (index) {
            if (index > 0 && (this.list[index - 1].league_id === this.list[index].league_id)) {
                return false
            } else {
                return true
            }
        },
        // 显示隐藏比赛数据
        toggleDetail (e) {
            let elem = e.currentTarget.querySelector('.play-detail')
            if (this.current === 0) {
                if (elem.style.height === '0px') {
                    elem.style.height = '100px'
                } else {
                    elem.style.height = '0px'
                }
            } else {
                if (elem.style.height === '0px') {
                    elem.style.height = '70px'
                } else {
                    elem.style.height = '0px'
                }
            }
        }
    }
}
