    font-size: 14px;
}
.tab-btn-group .tab-item:last-child {
    border-radius: 0 10px 10px 0;
    border-right: 1px solid #fff;
}
.tab-btn-group .tab-item:first-child {
    border-radius: 10px 0 0 10px;
}
.tab-btn-group .tab-item.tab-current {
    background: #fff;
    color: #18c4ba;
    font-weight: bolder;
}
.act-list {
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-touch-callout: none;
    user-select: none;
}
.act-list li{
    transition: 200ms;
    font-weight: normal;
    font-size: 14px;
    background: #fff;
    position: relative;
}
.act-list li::after {
    border-top: 1px solid #ddd;
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    right: auto;
    width: 100%;
    display: block;
    z-index: 15;
    transform-origin: 100% 100%;
    transform: scaleY(0.5);
}
.beizhu {
    font-size: 12px;
    text-align: right;
    padding-bottom: 10px;
    padding-right: 15px;
    color: #ccc;
}
.playe-line {
    display: flex;
}
.playe-line .player-name {
    flex: 1;
}
.playe-line .ci {
    font-size: 12px;
}
.playe-line .card-red {
    font-size: 12px;
    background: #ca3328;
    color: #fff;
    padding: 0 2px;
}
.playe-line .card-yellow {
    font-size: 12px;
    background: #fdc20d;
    color: #7e521f;
    padding: 0 2px;
}
.time-line {
    margin: 10px 0 10px;
    height: 10px;
    background: #ccc;
    position: relative;
    box-sizing: border-box;
    border-top: none;
}
.time-line .cd {
    background: #11af80;
    position: absolute;
    left: 0;
    height: 10px;
    bottom: 0;
}
.time-line:after {
    content: '';
    position: absolute;
    bottom: 0;
    border-left: 1px solid #666;
    left: 50%;
    height: 10px;
}
.time-line .icons i {
    transition: 200ms;
    transform: translate(-50%);
    z-index: 10;
    position: absolute;
    bottom: -2px;
    width: 15px;
    height: 15px;
    background-repeat: no-repeat;
    background-size: cover;
}

/*主队进球图标 */
.time-line .icons .z {
    background-image: url(../assets/images/hg.png);
}
/*客队进球图标 */
.time-line .icons .k {
    background-image: url(../assets/images/gg.png);
}
.time-line .icons .zs {
    bottom: 3px;
    background-image: url(../assets/images/zs.png);
}
.time-line .icons .ks {
    top: 3px;
    background-image: url(../assets/images/ks.png);
}
.ac-name {
    background: #f0f1f2;
    color: #aaa;
    padding: 0 10px;
    font-size: 12px;
    line-height: 3;
    position: relative;
}
.ac-name span {
    display: inline-block;
    vertical-align: bottom;
}
.ac-name .n {
    padding-right:10px;
    width: 100px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.ac-name .z {
    color:#cf1313
}
.ac-name .k {
    color:#2c1c91
}
.ac-name .b {
    color:#000
}
.play-detail {
    padding: 0 5px;
    transition: 300ms;
    height: 0;
    overflow-y: hidden;
    text-align: center;
    font-size: 12px;
}
.play-detail.show {
    height: 100px;
}
.play-detail.show2 {
    height: 70px;
}
.play-detail .cv-line {
    display: flex
}
.play-detail .cv-line .cv-helf {
    margin: 0 10px;
    flex: 1;
    display: flex;
    justify-content: space-between;
}
.play-detail .z {
    color: #cf1313;
}
.play-detail .t {
    color: #666;
    flex: 1;
}
.play-detail .t p{
    width: 130px;
    margin: auto;
    display: block;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
}
.play-detail .k {
    color: #2c1c91;
}
.play-detail div p {
    width: 100%;
}
.play-detail .rate-line {
    margin-bottom: 10px;
    background: #2c1c91;
    height: 3px;
}
.play-detail .rate-line i{
    display: block;
    width: 50%;
    background: #cf1313;
    height: 3px;
}
.ac-name::after {
    border-top: 1px solid #f0f1f3;
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    right: auto;
    width: 100%;
    display: block;
    z-index: 15;
    transform-origin: 100% 0%;
    transform: scaleY(0.5);
}

.ac-box {
    /*padding: 15px 0;*/
    display: flex;
    position: relative;
}
.ac-box .t {
    margin-right: 10px;
    color: #bbb;
}
.play-time {
    font-size: 18px;
    color: #d35b56;
    width: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.play-time .ccc .as {
    color: #000;
    font-size: 16px;
    text-align: center;
}
.play-time .ccc .vs {
    text-align: center;
    font-size: 20px;
}
.paly-score {
    width: 40px;
    display: flex;
    text-align: center;
    background: #f0f1f2;
    align-items: center;
    justify-content: center;
}
.paly-score .c1 {
}
.paly-score .c2 {
}
.paly-score .c3 {
    height: 30px;
}
.paly-content {
    flex: 1;
    line-height: 1.5;
    font-size: 12px;
    padding: 10px;
}
.play-line i{
    display: inline-block;
    height: 10px;
    background: #288267;
    width: 100%;
}
.page-detial {
    position: fixed;
    background: #efefef;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 9999;
}
.ui-border-b:after {
    border-top: 1px solid #ddd;
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    right: auto;
    width: 100%;
    display: block;
    z-index: 15;
    transform-origin: 100% 40%;
    transform: scaleY(0.5);
    -webkit-transform: scaleY(0.5);
}
</style>



