<template>
    <div class="ui-container">
        <div class="ui-nav-bar" style="background:#11af80;">
            <div class="tab-btn-group">
                <a class="tab-item" @click="current=0" :class="current==0?'tab-current':''">亚盘</a>
                <a class="tab-item" @click="current=1" :class="current==1?'tab-current':''">上半场绝杀</a>
                <a class="tab-item" @click="current=2" :class="current==2?'tab-current':''">下半场绝杀</a>
            </div>
        </div>
        <div class="ui-content recharge-page">
            <div class="page-warp">
                <div class="page-list">
                    <ul class="act-list">
                        <li v-for="(item, index) in list" :key="index" @click="toDetail(index)">
                            <div class="ac-name" v-if="showTitle(index)">{{item.league_name}}</div>
                            <div class="ac-box">
                                <div class="play-time">
                                    <span>{{item.cur_min}}'</span>
                                </div>
                                <div class="paly-content">
                                    <div class="playe-line">
                                        <div class="player-name" style="color:#cf1313">
                                            <span>{{item.home_name}}</span>
                                            <span class="card-red" v-if="item.home_redcard">{{item.home_redcard}}</span>
                                            <span class="card-yellow" v-if="item.home_yellowcard">{{item.home_yellowcard}}</span>
                                        </div>
                                        <div class="ci">
                                            {{item.rangfen_home_sp ? item.rangfen_home_sp : '--'}}/{{item.rangfen_handicap ? item.rangfen_handicap : '--'}}/{{item.rangfen_away_sp ? item.rangfen_away_sp : '--'}}
                                        </div>
                                        <div class="paly-score bi">{{item.home_goal}}:{{item.away_goal}}</div>
                                    </div>
                                    <div class="time-line">
                                        <div class="icons" v-html="calcZuqiu(item.events)"></div>
                                        <div class="cd" :style="calcTime(item.cur_min, item.league_ti)"></div>
                                    </div>
                                    <div class="playe-line">
                                        <div class="player-name" style="color:#2c1c91">
                                            <span>{{item.away_name}}</span>
                                            <span class="card-red" v-if="item.away_redcard">{{item.away_redcard}}</span>
                                            <span class="card-yellow" v-if="item.away_yellowcard">{{item.away_yellowcard}}</span>
                                        </div>
                                        <div class="ci">
                                            {{item.linchang_rangfen_home_sp ? item.linchang_rangfen_home_sp : '--'}}/{{item.linchang_rangfen_handicap ? item.linchang_rangfen_handicap : '--'}}/{{item.linchang_rangfen_away_sp ? item.linchang_rangfen_away_sp : '-'}}
                                        </div>
                                        <div class="paly-score she">{{item.home_shot}}:{{item.away_shot}}</div>
                                    </div>
                                </div>
                            </div>
                            <div :class="showIndex === index ? 'show' : 'show'" class="play-detail">
                                <div class="cv-line">
                                    <div class="cv-helf">
                                        <div class="z"><p>{{item['10_home_shot']}}</p></div>
                                        <div class="t"><p>最近10分钟总射门</p></div>
                                        <div class="k"><p>{{item['10_away_shot']}}</p></div>
                                    </div>
                                    <div class="cv-helf">
                                        <div class="z"><p>{{item['10_home_danger']}}</p></div>
                                        <div class="t"><p>最近10分钟危险进攻数</p></div>
                                        <div class="k"><p>{{item['10_away_danger']}}</p></div>
                                    </div>
                                </div>
                                <div class="cv-line">
                                    <div class="cv-helf">
                                        <p class="rate-line">
                                            <i :style="getRLW(item['10_home_shot'],item['10_away_shot'])"></i>
                                        </p>
                                    </div>
                                    <div class="cv-helf">
                                        <p class="rate-line">
                                            <i :style="getRLW(item['10_home_danger'],item['10_away_danger'])"></i>
                                        </p>
                                    </div>
                                </div>
                                <div class="cv-line">
                                    <div class="cv-helf">
                                        <div class="z"><p>{{item['20_home_shot']}}</p></div>
                                        <div class="t"><p>最近20分钟总射门</p></div>
                                        <div class="k"><p>{{item['20_away_shot']}}</p></div>
                                    </div>
                                    <div class="cv-helf">
                                        <div class="z"><p>{{item['20_home_danger']}}</p></div>
                                        <div class="t"><p>最近20分钟危险进攻数</p></div>
                                        <div class="k"><p>{{item['20_away_danger']}}</p></div>
                                    </div>
                                </div>
                                <div class="cv-line">
                                    <div class="cv-helf">
                                        <p class="rate-line">
                                            <i :style="getRLW(item['20_home_shot'],item['20_away_shot'])"></i>
                                        </p>
                                    </div>
                                    <div class="cv-helf">
                                        <p class="rate-line">
                                            <i :style="getRLW(item['20_home_danger'],item['20_away_danger'])"></i>
                                        </p>
                                    </div>
                                </div>
                                <div class="cv-line">
                                    <div class="cv-helf">
                                        <div class="z"><p>{{item.home_danger}}</p></div>
                                        <div class="t"><p>危险进攻数</p></div>
                                        <div class="k"><p>{{item.away_danger}}</p></div>
                                    </div>
                                </div>
                                <div class="cv-line">
                                    <div class="cv-helf">
                                        <p class="rate-line">
                                            <i :style="getRLW(item.home_danger,item.away_danger)"></i>
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import zuqiuDetail from './detail'
export default {
    data () {
        return {
            current: 0,
            showIndex: null,
            detailIndex: 0,
            showDetail: false,
            list: []
        }
    },
    components: {
        zuqiuDetail
    },
    mounted () {
        axios.get('/api/cur_match.json').then((data) => {
            this.list = data.data
        })
    },
    methods: {
        calcZuqiu (item) {
            let str = ''
            for (var i = 0; i < item.length; i++) {
                if (item[i][0] === 'home_shotoffgoal') {
                    // '主队射偏'
                    str += '<i class="zs" style="left:' + (item[i][1] / 60 / 0.9).toFixed() + '%"></i>'
                } else if (item[i][0] === 'home_shotongoal') {
                    // '主队射正'
                    str += '<i class="zs" style="left:' + (item[i][1] / 60 / 0.9).toFixed() + '%"></i>'
                } else if (item[i][0] === 'away_shotoffgoal') {
                    // '客队射偏'
                    str += '<i class="ks" style="left:' + (item[i][1] / 60 / 0.9).toFixed() + '%"></i>'
                } else if (item[i][0] === 'away_shotongoal') {
                    // '客队射正'
                    str += '<i class="ks" style="left:' + (item[i][1] / 60 / 0.9).toFixed() + '%"></i>'
                } else if (item[i][0] === 'home_goal') {
                    // '主队进球'
                    str += '<i class="z" style="left:' + (item[i][1] / 60 / 0.9).toFixed() + '%"></i>'
                } else if (item[i][0] === 'away_goal') {
                    // '客队进球'
                    str += '<i class="k" style="left:' + (item[i][1] / 60 / 0.9).toFixed() + '%"></i>'
                }
            }
            return str
        },
        calcTime (s, w) {
            if (s === '半') {
                return {width: '50%'}
            } else if (s === '全') {
                return {width: '100%'}
            } else if (s >= 90) {
                return {width: '100%'}
            } else {
                return {width: (s / 90) * 100 + '%'}
            }
        },
        getRLW (ratea, rateb) {
            return {width: `${((ratea / (ratea + rateb)) * 100).toFixed()}%`}
        },
        showTitle (index) {
            if (index > 0 && (this.list[index - 1].league_id === this.list[index].league_id)) {
                return false
            } else {
                return true
            }
        }
    }
}
</script>
<style>
.tab-btn-group {
    padding: 6px 0;
    display: flex;
    align-items: center;
    justify-content: center;
}
.tab-btn-group .tab-item {
    text-align: center;
    color: #fff;
    border: 1px solid #fff;
    border-right: 0px;
    line-height: 30px;
    width: 90px;
    font-size: 14px;
}
.tab-btn-group .tab-item:last-child {
    border-radius: 0 10px 10px 0;
    border-right: 1px solid #fff;
}
.tab-btn-group .tab-item:first-child {
    border-radius: 10px 0 0 10px;
}
.tab-btn-group .tab-item.tab-current {
    background: #fff;
    color: #18c4ba;
    font-size: 16px;
    font-weight: bolder;
}
.act-list {
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-touch-callout: none;
    user-select: none;
}
.act-list li{
    transition: 200ms;
    font-weight: normal;
    font-size: 14px;
    background: #fff;
    position: relative;
}
.act-list li::after {
    border-top: 1px solid #ddd;
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    right: auto;
    width: 100%;
    display: block;
    z-index: 15;
    transform-origin: 100% 100%;
    transform: scaleY(0.5);
}
.act-list li:active {
    background: #eee;
}
.beizhu {
    font-size: 12px;
    text-align: right;
    padding-bottom: 10px;
    padding-right: 15px;
    color: #ccc;
}
.playe-line {
    display: flex;
}
.playe-line .player-name {
    flex: 1;
}
.playe-line .ci {
    font-size: 12px;
    margin-right: 5px;
}
.playe-line .card-red {
    font-size: 12px;
    background: #ca3328;
    color: #fff;
    padding: 0 2px;
}
.playe-line .card-yellow {
    font-size: 12px;
    background: #fdc20d;
    color: #7e521f;
    padding: 0 2px;
}
.time-line {
    margin: 10px 0 10px;
    height: 10px;
    background: #ccc;
    position: relative;
    box-sizing: border-box;
    border-top: none;
}
.time-line .cd {
    background: #11af80;
    position: absolute;
    left: 0;
    height: 10px;
    bottom: 0;
}
.time-line:after {
    content: '';
    position: absolute;
    bottom: 0;
    border-left: 1px solid #666;
    left: 50%;
    height: 10px;
}
.time-line .icons i {
    transform: translate(-50%);
    z-index: 10;
    position: absolute;
    bottom: -2px;
    width: 15px;
    height: 15px;
    background-repeat: no-repeat;
    background-size: cover;
}

/*主队进球图标 */
.time-line .icons .z {
    background-image: url(../assets/images/hg.png);
}
/*客队进球图标 */
.time-line .icons .k {
    background-image: url(../assets/images/gg.png);
}
.time-line .icons .zs {
    bottom: 3px;
    background-image: url(../assets/images/zs.png);
}
.time-line .icons .ks {
    top: 3px;
    background-image: url(../assets/images/ks.png);
}
.ac-name {
    background: #f5f5f9;
    color: #aaa;
    padding: 0 10px;
    font-size: 12px;
    line-height: 3;
    position: relative;
}
.play-detail {
    padding: 0 5px;
    transition: 300ms;
    height: 0;
    overflow-y: hidden;
    text-align: center;
    font-size: 12px;
}
.play-detail.show {
    height: 100px;
}
.play-detail .cv-line {
    display: flex
}
.play-detail .cv-line .cv-helf {
    margin: 0 10px;
    flex: 1;
    display: flex;
    justify-content: space-between;
}
.play-detail .z {
    color: #cf1313;
}
.play-detail .t {
    flex: 1;
}
.play-detail .k {
    color: #2c1c91;
}
.play-detail div p {
    width: 100%;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
}
.play-detail .rate-line {
    margin-bottom: 10px;
    background: #2c1c91;
    height: 3px;
}
.play-detail .rate-line i{
    display: block;
    width: 50%;
    background: #cf1313;
    height: 3px;
}
.ac-name::after {
    border-top: 1px solid #f0f1f3;
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    right: auto;
    width: 100%;
    display: block;
    z-index: 15;
    transform-origin: 100% 0%;
    transform: scaleY(0.5);
}

.ac-box {
    padding: 15px 0;
    display: flex;
    position: relative;
}
.ac-box .t {
    margin-right: 10px;
    color: #bbb;
}
.play-time {
    font-size: 20px;
    color: #d35b56;
    width: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.paly-score.bi:before {
    content: '比分:';
    color: #aaa;
    font-size: 12px;
}
.paly-score.she:before {
    content: '射门:';
    color: #aaa;
    font-size: 12px;
}
.paly-content {
    flex: 1;
    line-height: 1.5;
    font-size: 12px;
    padding-right: 15px;
}
.play-line i{
    display: inline-block;
    height: 10px;
    background: #288267;
    width: 100%;
}
.page-detial {
    position: fixed;
    background: #efefef;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 9999;
}
.ui-border-b:after {
    border-top: 1px solid #ddd;
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    right: auto;
    width: 100%;
    display: block;
    z-index: 15;
    transform-origin: 100% 40%;
    transform: scaleY(0.5);
    -webkit-transform: scaleY(0.5);
}
</style>



